'use client';

import React, { useCallback, useEffect, useMemo, useRef } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { ChevronDownIcon } from 'lucide-react';
import { motion, useReducedMotion, type Variants } from 'framer-motion';

import { Button } from '../elements/button';
import { Heading } from '../elements/heading';
import { Subheading } from '../elements/subheading';

/* ──────────────────────────────────────────────────────────────
  Constants (no magic strings)
────────────────────────────────────────────────────────────── */
const GOLD = '#B4943E';
const DARK = '#1D1D1E';
const DEFAULT_SCROLL_ID = 'services';

const BG_IMG_CLASS =
  'object-cover filter grayscale saturate-0 contrast-125 brightness-[0.72]';

/* ──────────────────────────────────────────────────────────────
  Small color helper (for inline overlay styles)
────────────────────────────────────────────────────────────── */
const rgba = (hex: string, a: number) => {
  const h = hex.replace('#', '').trim();
  const full = h.length === 3 ? h.split('').map((c) => c + c).join('') : h;
  const n = parseInt(full, 16);
  const r = (n >> 16) & 255;
  const g = (n >> 8) & 255;
  const b = n & 255;
  return `rgba(${r}, ${g}, ${b}, ${a})`;
};

/* ──────────────────────────────────────────────────────────────
  Strapi helpers
────────────────────────────────────────────────────────────── */
const STRAPI_BASE = (
  process.env.NEXT_PUBLIC_STRAPI_URL ??
  process.env.NEXT_PUBLIC_API_URL ??
  ''
).replace(/\/$/, '');

const toAbs = (u?: string | null) => {
  if (!u) return '';
  if (u.startsWith('http')) return u;
  return STRAPI_BASE ? `${STRAPI_BASE}${u}` : u;
};

/* ──────────────────────────────────────────────────────────────
  Types
────────────────────────────────────────────────────────────── */
type CTA = {
  id: number;
  text: string;
  URL: string;
  target?: string | null; // pl. "_blank"
  variant?: 'primary' | 'outline' | 'muted' | 'simple' | string;
};

type StrapiImageFormat = {
  url?: string | null;
  width?: number | null;
  height?: number | null;
};

type StrapiMediaImage = {
  id: number;
  url?: string | null;
  alternativeText?: string | null;
  formats?: {
    thumbnail?: StrapiImageFormat;
    small?: StrapiImageFormat;
    medium?: StrapiImageFormat;
    large?: StrapiImageFormat;
    [key: string]: StrapiImageFormat | undefined;
  } | null;
};

type Props = {
  heading: string;
  sub_heading: string;
  CTAs: CTA[];
  locale: string;
  image?: StrapiMediaImage | null;

  // App Router friendly scroll cue
  scrollToId?: string;

  // optional parallax
  parallax?: boolean;
};

export const Hero = ({
  heading,
  sub_heading,
  CTAs,
  locale,
  image,
  scrollToId = DEFAULT_SCROLL_ID,
  parallax = false,
}: Props) => {
  /* ──────────────────────────────────────────────────────────────
    Image
  ─────────────────────────────────────────────────────────────── */
  const preferred =
    image?.formats?.large?.url ||
    image?.formats?.medium?.url ||
    image?.formats?.small?.url ||
    image?.url ||
    null;

  const imgSrc = toAbs(preferred);
  const hasImage = Boolean(imgSrc);
  const imgAlt = image?.alternativeText || heading || 'Hero image';

  const withLocale = (url: string) => {
    const path = url?.startsWith('/') ? url : `/${url || ''}`;
    return `/${locale}${path}`.replace(/\/{2,}/g, '/');
  };

  const toButtonVariant = (
    v?: string | null
  ): 'simple' | 'outline' | 'primary' | 'muted' => {
    return v === 'simple' || v === 'outline' || v === 'primary' || v === 'muted'
      ? v
      : 'primary';
  };

  /* ──────────────────────────────────────────────────────────────
    Motion (stagger)
  ─────────────────────────────────────────────────────────────── */
  const reduceMotion = useReducedMotion();

  const contentVariants: Variants = useMemo(
    () => ({
      hidden: { opacity: 0 },
      show: {
        opacity: 1,
        transition: reduceMotion
          ? { duration: 0.01 }
          : { delayChildren: 0.35, staggerChildren: 0.14 },
      },
    }),
    [reduceMotion]
  );

  const itemVariants: Variants = useMemo(
    () => ({
      hidden: { opacity: 0, y: 16 },
      show: {
        opacity: 1,
        y: 0,
        transition: reduceMotion
          ? { duration: 0.01 }
          : { duration: 0.9, ease: [0.22, 1, 0.36, 1] },
      },
    }),
    [reduceMotion]
  );

  /* ──────────────────────────────────────────────────────────────
    Optional parallax (clean rAF)
  ─────────────────────────────────────────────────────────────── */
  const bgRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!parallax || reduceMotion) return;
    let raf = 0;

    const onScroll = () => {
      if (raf) return;
      raf = window.requestAnimationFrame(() => {
        raf = 0;
        const y = window.scrollY * 0.25; // subtle, old vibe
        if (bgRef.current) {
          bgRef.current.style.transform = `translate3d(0, ${-y}px, 0)`;
        }
      });
    };

    onScroll();
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => {
      window.removeEventListener('scroll', onScroll);
      if (raf) window.cancelAnimationFrame(raf);
    };
  }, [parallax, reduceMotion]);

  /* ──────────────────────────────────────────────────────────────
    Scroll cue
  ─────────────────────────────────────────────────────────────── */
  const onScrollCue = useCallback(() => {
    if (!scrollToId) return;
    document.getElementById(scrollToId)?.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
    });
  }, [scrollToId]);

  const showScrollCue = Boolean(scrollToId);

  /* ──────────────────────────────────────────────────────────────
    Overlay styles (Tailwind-safe: inline)
  ─────────────────────────────────────────────────────────────── */
  const overlayLTRStyle = useMemo(
    () => ({
      backgroundImage: `linear-gradient(to right, ${rgba(DARK, 0.95)}, ${rgba(
        DARK,
        0.8
      )}, transparent)`,
    }),
    []
  );

  const overlayDarkStyle = useMemo(
    () => ({
      backgroundColor: rgba(DARK, 0.2),
    }),
    []
  );

  const overlayRadialStyle = useMemo(
    () => ({
      backgroundImage:
        'radial-gradient(circle at 25% 20%, rgba(255,255,255,0.10), transparent 60%)',
    }),
    []
  );

  return (
    <section className="relative isolate min-h-[100svh] overflow-hidden bg-black">
      {/* Background (single layer) */}
      <div
        ref={bgRef}
        className="absolute inset-0 -z-10 will-change-transform"
        aria-hidden="true"
      >
        {hasImage ? (
          <Image
            src={imgSrc}
            alt={imgAlt}
            fill
            priority
            sizes="100vw"
            className={BG_IMG_CLASS}
          />
        ) : (
          <div className="absolute inset-0 bg-gradient-to-b from-black via-neutral-950 to-black" />
        )}
      </div>

      {/* Overlay stack (old logic) */}
      <div className="absolute inset-0 -z-10" aria-hidden="true">
        <div className="absolute inset-0" style={overlayLTRStyle} />
        <div className="absolute inset-0" style={overlayDarkStyle} />
        <div className="absolute inset-0" style={overlayRadialStyle} />
      </div>

      {/* Design lines (decor only) */}
      <div className="pointer-events-none absolute inset-0 z-0" aria-hidden="true">
        {/* Left animated line: 0 -> 25% */}
        <motion.div
          initial={reduceMotion ? { width: '25%', opacity: 1 } : { width: 0, opacity: 0 }}
          animate={{ width: '25%', opacity: 1 }}
          transition={
            reduceMotion
              ? { duration: 0.01 }
              : { duration: 1.2, delay: 0.5, ease: [0.22, 1, 0.36, 1] }
          }
          className="absolute left-0 top-1/2 h-px -translate-y-1/2"
          style={{
            backgroundImage: `linear-gradient(to right, ${GOLD}, transparent)`,
          }}
        />
      </div>

      {/* Content (single wrapper) */}
      <div className="relative z-10 mx-auto flex min-h-[100svh] max-w-7xl items-center px-4 py-16 md:px-8">
        <motion.div
          variants={contentVariants}
          initial="hidden"
          animate="show"
          className="w-full"
        >
          <div className="relative max-w-3xl">
            {/* Subtle vertical accent lines left (old premium vibe) */}
            <div
              className="pointer-events-none absolute -left-6 top-3 hidden h-16 w-8 md:block"
              aria-hidden="true"
            >
              <div className="absolute left-0 top-0 h-full w-px bg-white/30" />
              <div className="absolute left-2 top-0 h-full w-px" style={{ backgroundColor: GOLD }} />
            </div>

            {/* Heading */}
            <motion.div variants={itemVariants} className="relative">
              <Heading
                as="h1"
                className="text-left text-4xl font-semibold leading-[1.05] text-white md:text-5xl lg:text-7xl"
              >
                {heading}

                {/* Gold line to the right of heading (old) */}
                <motion.span
                  aria-hidden="true"
                  initial={reduceMotion ? { width: 96, opacity: 1 } : { width: 0, opacity: 0 }}
                  animate={{ width: 96, opacity: 1 }}
                  transition={
                    reduceMotion
                      ? { duration: 0.01 }
                      : { duration: 1, delay: 1.2, ease: [0.22, 1, 0.36, 1] }
                  }
                  className="absolute -right-10 top-1/2 hidden h-px md:block"
                  style={{ backgroundColor: GOLD }}
                />
              </Heading>

              {/* Gold + soft white line under heading */}
              <div className="pointer-events-none mt-4 flex items-center gap-3" aria-hidden="true">
                <motion.span
                  initial={reduceMotion ? { width: 96, opacity: 1 } : { width: 0, opacity: 0 }}
                  animate={{ width: 96, opacity: 1 }}
                  transition={
                    reduceMotion
                      ? { duration: 0.01 }
                      : { duration: 0.9, delay: 0.35, ease: [0.22, 1, 0.36, 1] }
                  }
                  className="h-px shrink-0"
                  style={{ backgroundColor: GOLD }}
                />
                <span className="h-px w-24 bg-white/25" />
              </div>
            </motion.div>

            {/* Sub / description */}
            <motion.div variants={itemVariants} className="mt-4 md:mt-6">
              <Subheading className="text-left text-base text-white/80 md:text-xl">
                {sub_heading}
              </Subheading>
            </motion.div>

            {/* CTAs */}
            <motion.div variants={itemVariants} className="mt-8 flex flex-wrap gap-3">
              {CTAs?.map((cta) => {
                const href = withLocale(cta.URL);
                const target = cta.target || undefined;
                const rel = target === '_blank' ? 'noopener noreferrer' : undefined;

                return (
                  <Button key={cta.id} variant={toButtonVariant(cta.variant)} asChild>
                    <Link href={href} target={target} rel={rel}>
                      {cta.text}
                    </Link>
                  </Button>
                );
              })}
            </motion.div>
          </div>
        </motion.div>
      </div>

      {/* Scroll cue (single) */}
      {showScrollCue && (
        <div className="absolute inset-x-0 bottom-6 z-20 flex justify-center px-4">
          <button
            type="button"
            onClick={onScrollCue}
            aria-label="Görgess lejjebb"
            className="group flex flex-col items-center gap-2 rounded-full px-4 py-3 text-white/70 transition hover:text-white/90 focus:outline-none focus-visible:ring-2 focus-visible:ring-white/40"
          >
            <span className="text-xs font-light tracking-wider md:text-sm">
              Görgess lejjebb
            </span>

            <motion.span
              aria-hidden="true"
              animate={reduceMotion ? undefined : { y: [0, 10, 0] }}
              transition={
                reduceMotion
                  ? { duration: 0.01 }
                  : { duration: 1.5, repeat: Infinity, ease: 'easeInOut' }
              }
              className="inline-flex"
            >
              <ChevronDownIcon className="h-5 w-5" />
            </motion.span>
          </button>
        </div>
      )}

      {/* Bottom fade */}
      <div
        className="pointer-events-none absolute inset-x-0 bottom-0 h-40 bg-gradient-to-t from-black/45 to-transparent"
        aria-hidden="true"
      />
    </section>
  );
};
